/*=============== Contact container ===============*/
.Contact{
    height: 90vh;
    margin: 0;
    padding: 0;
    background-color: #121212;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:50px;
}

/*=============== Image  ===============*/
.Heart{
    height: 450px;
    width: 400px;
    filter: drop-shadow(1px 1px 7px #E0E0E0);
}

/*=============== Form  ===============*/
form{
    font-family: system-ui,sans-serif;
    max-width: 600px;
    width: 100%;
    background: #fff;
    padding: 25px 25px 30px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgb(0, 0, 0, .1);
    color: #333;
    margin: 25px;
}

form h2{
    font-size: 30px;
    text-align: center;
}

form .input-box{
    margin-top: 20px;
}

.input-box .field{
    width: 100%;
    height: 50px;
    background: transparent;
    border: 2px solid #ddd;
    outline: none;
    border-radius: 6px;
    padding: 15px;
    font-size: 16px;
    color: #333;
    margin-top: 8px;
}

.input-box .field.mess{
    height: 200px;
    resize: none;
}

form button{
    width: 100%;
    height: 55px;
    background: #826afb;
    border: none;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    margin-top: 15px;
    transition: .5s;
}
form button:hover{
    background: #624ec6;
}

@media screen and (max-width: 900px){
    form{
        width: 75%;
    }

    .Contact{
        gap: 0;
    }

    .Heart{
        display: none;
    }
}
